<template>
	<view class="box">
		<!-- 返回按钮 -->
		<view class="back" @click="handleBack">
			<image src="@/static/img/icon_back@3x.png" mode=""></image>
		</view>
		<!-- <view class="shop" :style="{'backgroundImage': 'url(@/static/img/bg@3x.png)',
		  'backgroundRepeat':'no-repeat', 'backgroundSize':'100% 100%'}"> -->

		<!-- 头部 -->
		<view class="user-top">
			<view class="user">
				<view class="user-left">
					<image :src="userData.Image?`https://farm.juzhentech.com${userData.Image}` : img" mode=""></image>
					<view class="text">
						<view class="">{{userData.NickName || '未登录'}}</view>
						<view class="">{{userData.FarmCode||''}}</view>
					</view>
				</view>
				<view class="user-message">
					<view class="number-message" @click="handleMessage">
						<view class="number">{{userData.MessageCount||0}}</view>
						<image src="@/static/img/icon_message@3x.png" mode=""></image>
						<view class="message">消息</view>
					</view>
				</view>
			</view>
			<view class="user-work">
				<view class="title" @click="handleWork(-1)">我的劳作</view>
				<view class="tabs-work">
					<view class="tabs" v-for="(item,index) in worklist" :key="index" @click="handleWork(index)">
						<view class="work-number">{{item.count}}</view>
						<image :src="item.img" mode=""></image>
						<view class="text">{{item.text}}</view>
					</view>
				</view>
			</view>
		</view>

		<!-- 收获模块 -->
		<view class="reaps-order">
			<view class="title" @click="handleHarvest(-1)">收获订单</view>
			<view class="tabs-work">
				<view class="tabs" v-for="(item,index) in reapslist" :key="index" @click="handleHarvest(index)">
					<view class="work-number">{{item.count}}</view>
					<image :src="item.img" mode=""></image>
					<view class="text">{{item.text}}</view>
				</view>
			</view>
		</view>


		<view class="list">
			<view class="tabs-list" v-for="(item,index) in userlist" :key="index"
				@click="handleClick(item.url,item.popup)">
				<view class=" list-left">
					<image :src="item.img" mode=""></image>
					<text>{{item.text}}</text>
				</view>
				<image src="@/static/img/icon_go@3x.png" mode=""></image>
			</view>
		</view>

		<!-- 种苗弹窗 -->
		<uni-popup ref="popup" type="bottom">
			<view class="popup">
				<view class="title-close">
					<view class="title">种苗更新</view>
					<image src="@/static/img/icon_close@3x.png" mode="" @click="$refs.popup.close()"></image>
				</view>
				<view class="present"><text>现种种类：{{landList.SeedlingsName||''}}</text></view>
				<view class="type">
					<view class="title">更换类型</view>
					<view class="type-list">
						<view class="list-number" :class="{'active':typeIndex===index}" v-for="(item,index) in typeList"
							:key="index" @click="handleType(index)">
							<image :src="item.Image?`https://farm.juzhentech.com${item.Image}`:''" mode=""></image>
							<view class="">{{item.SeedlingsName}}</view>
						</view>
					</view>
				</view>
				<view class="bottom">
					<view class="btn" @click="updateSeedling">确认更换</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {
		token
	} from '@/apis/api';
	import img from '@/static/img/bg@3x.png'
	import {
		GetUserInfoAjax,
		GetSeedlingDtoAjax,
		PostUpdateLandSeedlingAjax
	} from '@/apis/api/modules/user-center'
	export default {
		data() {
			return {
				// img,
				img: 'https://thirdwx.qlogo.cn/mmopen/vi_32/POgEwh4mIHO4nibH0KlMECNjjGxQUq24ZEaGT4poC6icRiccVGKSyXwibcPq4BWmiaIGuG1icwxaQX6grC9VemZoJ8rg/132',
				worklist: [{
						img: require('@/static/img/icon_nopay@3x.png'),
						text: '待付款',
						count: 0
					},
					{
						img: require('@/static/img/icon_work@3x.png'),
						text: '待劳作',
						count: 0
					},
					{
						img: require('@/static/img/icon_evaluation@3x.png'),
						text: '待评价',
						count: 0
					},
					{
						img: require('@/static/img/icon_finish@3x.png'),
						text: '已完成',
						count: 0
					},
				],
				reapslist: [{
						img: require('@/static/img/icon_ontheway@3x.png'),
						text: '待发货',
						count: 0
					}, {
						img: require('@/static/img/icon_ontheway@3x.png'),
						text: '待收货',
						count: 0
					},
					{
						img: require('@/static/img/icon_evaluation@3x.png'),
						text: '待评价',
						count: 0
					},
					{
						img: require('@/static/img/icon_finish@3x.png'),
						text: '已完成',
						count: 0
					},
				],
				userlist: [{
						img: require('@/static/img/icon_collection@3x.png'),
						text: '我的收藏',
						url: '/pages/user-center/user-collection/index?Name=collection'
					},
					{
						img: require('@/static/img/icon_code@3x.png'),
						text: '我的活动',
						url: '/pages/user-center/user-activity/index'
					},
					{
						img: require('@/static/img/icon_adress@3x.png'),
						text: '我的地址',
						url: '/pages/user-center/user-address/index'
					},
					{
						img: require('@/static/img/icon_seeds@3x.png'),
						text: '种苗更新',
						url: '',
						popup: true
					}
				],
				typeIndex: null,
				typeList: [],
				landList: '',
				seedlingsID: '',
				userData: ''
			}
		},
		onShow() {
			this.getUserData()
		},
		methods: {
			async getUserData() {
				const result = await GetUserInfoAjax({
					token: token.value ? true : false
				})
				this.userData = result.Result
				this.worklist[0].count = this.userData.Lobor_WaitPay
				this.worklist[1].count = this.userData.Lobor_WaitLobor
				this.worklist[2].count = this.userData.Lobor_WaitEva
				this.worklist[3].count = this.userData.Lobor_Over
				this.reapslist[0].count = this.userData.Havest_WaitDeliver
				this.reapslist[1].count = this.userData.Havest_WaitReceipt
				this.reapslist[2].count = this.userData.Havest_WaitEva
				this.reapslist[3].count = this.userData.Havest_Over

			},
			handleBack() {
				uni.navigateBack({
					delta: 1
				})
			},
			/**
			 * 消息
			 */
			handleMessage() {
				uni.navigateTo({
					url: '/pages/user-center/user-message/index'
				})
			},
			/**
			 * 我的劳作
			 */
			handleWork(index) {
				uni.navigateTo({
					url: `/pages/user-center/user-work/index?index=${index}`
				})
			},
			/**
			 * 收获订单
			 */
			handleHarvest(index) {
				uni.navigateTo({
					url: `/pages/user-center/harvest-order/index?index=${index}`
				})
			},
			/**
			 * 
			 * @param {Object} url
			 */
			async handleClick(url, popup) {
				if (url !== undefined) {
					uni.navigateTo({
						url: url
					})
				}

				if (popup === true) {
					this.$refs.popup.open()
					const result = await GetSeedlingDtoAjax({
						token: true
					})
					this.landList = result.Result.LandList[0]
					this.typeList = result.Result.List
				}
			},
			/**
			 * 选择种类
			 * @param {Object} i
			 */
			handleType(i) {
				this.typeIndex = i
				this.landList.SeedlingsName = this.typeList[i].SeedlingsName
				this.seedlingsID = this.typeList[i].SeedlingsID
			},
			async updateSeedling() {
				if (!this.seedlingsID) {
					uni.showToast({
						title: '请选择种苗！',
						icon: 'error'
					})
					return false
				}
				const result = await PostUpdateLandSeedlingAjax({
					LandID: this.landList.LandID,
					SeedlingsID: this.seedlingsID,
					token: true
				})
				uni.showToast({
					title: result.Result,
					icon: 'none'
				})
				this.typeIndex = null
				this.landList.SeedlingsName = ''
				this.seedlingsID = ''
				this.$refs.popup.close()
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		background: #F7F7F7;
	}

	.back {
		position: absolute;
		top: 66rpx;
		left: 30rpx;
		z-index: 20;

		image {
			width: 44rpx;
			height: 44rpx;
		}
	}

	.user-top {
		width: 750rpx;
		height: 548rpx;
		background: url(@/static/img/userbg.png) no-repeat;
		background-size: 100% 100%;
		padding: 160rpx 30rpx 0;
		box-sizing: border-box;

		.user {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.user-left {
				display: flex;
				justify-content: start;
				align-items: center;

				image {
					width: 120rpx;
					height: 120rpx;
					border-radius: 50%;
				}

				.text {
					color: #FFFFFF;
					padding-left: 30rpx;

					view:nth-of-type(1) {
						font-size: 36rpx;
						font-weight: 500;
						line-height: 50rpx;
						padding-bottom: 10rpx;
					}

					view:nth-of-type(2) {
						font-size: 28rpx;
						font-weight: 400;
						line-height: 40rpx;
					}
				}
			}

			.user-message {
				.number-message {
					position: relative;

					.number {
						text-align: center;
						position: absolute;
						width: 32rpx;
						height: 32rpx;
						background: #CF1200;
						font-size: 20rpx;
						font-weight: 500;
						color: #FFFFFF;
						line-height: 32rpx;
						border-radius: 50%;
						z-index: 5;
						top: -14rpx;
						left: -2rpx;
					}

					.message {
						font-size: 24rpx;
						font-weight: 500;
						color: #FFFFFF;
						line-height: 34rpx;
					}
				}

				image {
					width: 60rpx;
					height: 60rpx;
				}
			}
		}

		.user-work {
			margin-top: 30rpx;
			padding: 20rpx;
			background: #FFFFFF;
			border-radius: 16rpx;

			.title {
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
				border-bottom: 2rpx solid #EFEFEF;
				padding-bottom: 20rpx;
			}
		}
	}

	.tabs-work {
		display: flex;
		justify-content: space-around;
		padding: 30rpx 0 10rpx;

		.tabs {
			position: relative;

		}

		.work-number {
			position: absolute;
			text-align: center;
			line-height: 30rpx;
			top: 0;
			right: 0;
			min-width: 30rpx;
			height: 30rpx;
			background: red;
			border-radius: 50%;
			color: #fff;
			font-size: 22rpx;
			z-index: 5;
		}

		image {
			width: 70rpx;
			height: 70rpx;
		}
	}

	.reaps-order {
		background: #FFFFFF;
		border-radius: 16rpx;
		margin: 50rpx 30rpx 0;
		padding: 20rpx;

		.title {
			font-size: 28rpx;
			font-weight: 500;
			color: #333333;
			line-height: 40rpx;
			border-bottom: 2rpx solid #EFEFEF;
			padding-bottom: 20rpx;
		}
	}

	.list {
		background: #FFFFFF;
		border-radius: 16rpx;
		margin: 30rpx;
		padding: 0 20rpx;

		.tabs-list {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 2rpx solid #F2F4F7;
			padding: 30rpx 0;

			.list-left {
				display: flex;
				align-items: center;

				image {
					width: 40rpx;
					height: 40rpx;
					margin-right: 20rpx;
				}
			}

			image {
				width: 36rpx;
				height: 36rpx;
			}

			&:nth-of-type(4) {
				border: none;
			}
		}
	}

	.popup {
		background: #fff;
		border-radius: 30rpx 30rpx 0px 0px;
		// padding: 30rpx;

		.title-close {
			display: flex;
			justify-content: space-between;
			padding-top: 30rpx;
			margin: 0 30rpx 0;

			.title {
				width: 60%;
				text-align: end;
				font-size: 34rpx;
				font-weight: 500;
				color: #333333;
				line-height: 48rpx;
			}

			image {
				width: 48rpx;
				height: 48rpx;
			}
		}

		.present {
			background: rgba(0, 248, 143, 0.1);
			border-radius: 20rpx;
			padding: 30rpx;
			box-sizing: border-box;
			margin: 40rpx 30rpx 0;

			text {
				width: 224px;
				font-weight: 500;
				color: #00D08F;
				line-height: 44rpx;
			}
		}

		.type {
			padding-top: 30rpx;
			margin: 0 30rpx 6rpx;

			.title {
				text-align: center;
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
			}

			.type-list {
				display: flex;
				justify-content: space-around;
				flex-wrap: wrap;
				text-align: center;
				padding-top: 28rpx;

				.list-number {
					box-sizing: border-box;
					padding: 25rpx 40rpx 15rpx;
					border-radius: 20rpx;
					background: rgba(0, 0, 0, 0.05);
					margin-bottom: 30rpx;
				}

				.active {
					background: rgba(0, 208, 143, 0.05);
					border: 2rpx solid #00D08F;
				}

				image {
					width: 63rpx;
					height: 63rpx;
				}
			}
		}

		.bottom {
			background: #FFFFFF;
			box-shadow: 0px 4rpx 20rpx 0px rgba(2, 102, 71, 0.15);
			border-radius: 30rpx 30rpx 0px 0px;
			padding: 30rpx;

			.btn {
				text-align: center;
				background: #00D08F;
				border-radius: 52rpx;
				backdrop-filter: blur(20rpx);
				padding: 30rpx 0;
				font-size: 32rpx;
				font-weight: 500;
				color: #FFFFFF;
			}
		}
	}
</style>
